
import { useEffect, useRef, useState } from 'react';
import styles from './index.less'
import PauseIcon from './PauseIcon';
import PlayIcon from './PlayIcon';
import Stop from './Stop';
import VoicePause from './VoicePause';
import VoicePlay from './VoicePlay';
// 计算播放比例  audio  进度条的宽度 * 比例

const formatNumberWith0 = (n:number) => {
    return n > 10 ? n: `0${n}`
}
const getTime = (seconds:number): string => {
    if (seconds < 60) {
        const time = Math.round(seconds);
        return `00:${time < 10 ? `0${time}` : time}`;
    }
    const m = Math.round(seconds / 60);
    const s = Math.round(seconds - (m * 60)) < 0 ? 0 : Math.round(seconds - (m * 60));
    return `${formatNumberWith0(m)}:${formatNumberWith0(s)}`;
};

// 可能 timeupdate 比定时器好 不能自动播放
const AudioPlay = (props:{url: string}) => {
    const {url} = props;

    const audioRef = useRef<HTMLAudioElement | null>(null);
    const progressRef = useRef<HTMLDivElement | null>(null);
    const [state, setState] = useState({
        curTime: 0, // 记录状态
        allTime: 0,
        isPlaying: false, // 点击做判断
        isHaveVoice: true, // 点击做判断
        proWidth: 0, // 页面显示
        loaded: false,
    })

    const stateRef = useRef({
        playTimer: 0,
        lastVolume: 1,
    }) as any;

    const refreshTime = () => {
    

        if (audioRef.current && progressRef.current) {
            let rect = progressRef.current.getBoundingClientRect();
            const audio = audioRef.current;
            const curTime = audio.currentTime;
            const allTime = audio.duration;
            const percent = allTime === 0 ? 0 : parseFloat((curTime / allTime).toFixed(2));
            const proWidth = percent * rect.width;
            setState((old) => ({
                ...old,
                curTime,
                allTime,
                proWidth
            }))
            // console.log(audio.currentTime);

        }


    }

    useEffect(() => {
        if (audioRef.current) {
            const audio = audioRef.current;
            const refreshPlayBtn = (e: { type: string; }) => {
                setState((old) => ({
                    ...old,
                    isPlaying: e.type === 'play'
                }))
            }
            const initPlay = () => {
                setState((old) => ({
                    ...old,
                    curTime: audio.currentTime,
                    allTime: audio.duration,
                    isHaveVoice: audio.volume > 0,
                }))

                audio.addEventListener('play', refreshPlayBtn);
                audio.addEventListener('pause', refreshPlayBtn)
                // if(autoPlay) {
                //     // stateRef.current.playTimer =  setInterval(()=>{

                //     // }, 1000)
                //     // audio.play();
                // }
            }

            const handleEnd = () => {
                clearInterval(stateRef.current.playTimer)
            }

            audio.addEventListener('loadeddata', initPlay)
            audio.addEventListener('error', () => { })
            audio.addEventListener('ended', handleEnd)

            return () => {
                audio.removeEventListener('loadeddata', initPlay);
                audio.removeEventListener('error', () => { })
                audio.removeEventListener('play', refreshPlayBtn);
                audio.removeEventListener('pause', refreshPlayBtn)
                audio.removeEventListener('ended', handleEnd)
            }
        }
    }, []);

    const handlePlay = () => {
        if (audioRef.current) {
            const audio = audioRef.current;
            if(!state.isPlaying){
                //不需要
                // audio.currentTime = state.curTime;
                stateRef.current.playTimer = setInterval(refreshTime, 1000)
                audio.play();
            } else {
                clearInterval(stateRef.current.playTimer);
                audio.pause();
            }
           
        }
    }

    const handleVoice = () => {
        if (audioRef.current) {
            const audio = audioRef.current;
            if (state.isHaveVoice) {
                stateRef.current.lastVolume = audio.volume;
                audio.volume = 0;
            } else {
                audio.volume = stateRef.current.lastVolume || 0.5
            }
            setState((old) => ({
                ...old,
                isHaveVoice: !old.isHaveVoice
            }))

        }
    }
    const handleStop = () => {
        if (audioRef.current) {
            const audio = audioRef.current;
            audio.currentTime = 0;
            //  监听数事件 让按钮开始
            audio.pause();
            refreshTime();
            clearInterval(stateRef.current.playTimer)
        }
    }

    const handleJump =(e:any) => {
           
            // 将音频指向 时间段
            if(audioRef.current && progressRef.current) {
                let rect = progressRef.current.getBoundingClientRect();
                const audio = audioRef.current;
                const innerLeft = e.clientX - rect.left;
                const percent = parseFloat((innerLeft / rect.width).toFixed(2));
                audio.currentTime = audio.duration * percent;
                
                clearInterval(stateRef.current.playTimer)
                stateRef.current.playTimer = setInterval(refreshTime, 1000)
                audio.play();
                refreshTime();
            }

    }

    return (
        <div className={styles.view}>
            <audio src={url} ref={audioRef}>
                <track kind="captions" />
            </audio>
            <div className={styles.action}>
                <i className={styles.stop} onClick={handleStop}>
                    <Stop className={styles.stopIcon} />
                </i>
                <i className={styles.play} onClick={handlePlay}>

                    {
                        state.isPlaying ? <PlayIcon className={styles.playIcon} /> : <PauseIcon className={styles.playIcon} />
                    }


                </i>
                <i className={styles.voice} onClick={handleVoice}>
                    {
                        state.isHaveVoice ? <VoicePlay className={styles.voiceIcon} /> : <VoicePause className={styles.voiceIcon} />
                    }


                </i>
            </div>
            <div style={{ marginLeft: 8 }}>
                {getTime(state.curTime)}/{getTime(state.allTime)}
            </div>
            <div className={styles.line} ref={progressRef} onClick={handleJump}>
                <div className={styles.progress} style={{ width: state.proWidth }}></div>
                <div className={styles.btn} style={{ left: state.proWidth }}></div>
            </div>
        </div>
    )
}


export default AudioPlay;

// 不变的封装起来
// 进度不会变
