import Text from "component/Text";
import styles from "./index.less";
import FlexContainer from "component/FlexContainer";
import FlexItem from "component/FlexItem";

const PropsTable = (props: any) => {
	const { title, desc, list } = props;

	return (
		<section>
			<Text level="h2" className={styles.header}>
				{title}
			</Text>
			<Text>{desc}</Text>
			<FlexContainer flex="horizontal-full" className={styles.tableHead}>
				<FlexItem basis="20%">
					<Text className={styles.text}>参数</Text>
				</FlexItem>
				<FlexItem basis="35%">
					<Text className={styles.text}>说明</Text>
				</FlexItem>
				<FlexItem basis="30%">
					<Text className={styles.text}>类型</Text>
				</FlexItem>
				<FlexItem basis="15%">
					<Text className={styles.text}>默认值</Text>
				</FlexItem>
			</FlexContainer>
			{list.map((item: any) => (
				<FlexContainer flex="horizontal-full" key={item.name} className={styles.rowContainer}>
					<FlexItem basis="20%" className={styles.rowCenter}>
						<Text className={styles.row}>{item.name}</Text>
					</FlexItem>
                    <FlexItem basis="35%" className={styles.rowCenter}>
						<Text className={styles.row}>{item.desc}</Text>
					</FlexItem>
                    <FlexItem basis="30%" className={styles.rowCenter}>
						<Text className={styles.row}>{
                        Array.isArray(item.type) ? item.type.map((sub:any)=> <span key={sub} className={styles.cicleItem}>{sub}</span>): item.type
    
                    }</Text>
					</FlexItem>
                    <FlexItem basis="15%" className={styles.rowCenter}>
						<Text className={styles.row}>{item.defValue}</Text>
					</FlexItem>

				</FlexContainer>
			))}
		</section>
	);
};

export default PropsTable;
